@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
  color: inherit;
  text-decoration: none;
}


html, body{
  background: #F9F8FD;
  font-family: 'Manrope', sans-serif;
}

.container{
  margin: 0 auto;
  max-width: 1400px;
  padding: 0 24px;
  width: 100%;
}

.flama{
  align-items: center;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  min-height: 100vh;
}
header {
  align-items: center;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  height: 64px;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1;
}

header .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.site-nav, .main-navigation {
  align-items: center;
  display: flex;
  height: 100%;
}

.main-navigation ul {
  align-items: center;
  display: flex;
  margin-left: 32px;
  height: 100%;
}

.main-navigation ul li {
  align-items: center;
  display: flex;
  height: 100%;
  position: relative;
}

.main-navigation ul li:after {
  background: #ea4c4c;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  transition: 0.35s ease-in-out;
  width: 0%;
}

.main-navigation ul li.active:after {
  width: 100%;
}

.main-navigation ul li:hover a{
  color: #000;
}

.main-navigation ul li.active a{
  color: #ea4c4c;
}

.main-navigation ul li a {
  color: rgba(0,0,0,0.55);
  font-size: 14px;
  font-weight: 600;
  padding: 0 16px;
  transition: 0.3s ease-in-out;
  width: 100%;
}

.logo {
  transform: translateY(-2px);
  width: 88px;
}

.balance-wr {
  align-items: center;
  display: flex;
}

.balance-wr small {
  font-size: 14px;
}

.balance-wr li {
  align-items: center;
  display: flex;
  padding-left: 16px;
}

.balance-wr li b {
  font-size: 14px;
  font-weight: 500;
}

.balance-wr li b span {
  font-weight: 600;
}

.balance-wr li img {
  width: 21px;
  margin-right: 4px;
}

@media all and (max-width: 860px) {
  .balance-wr small {
    display: none;
  }
}

@media all and (max-width: 720px) {
  .main-navigation{
    display: none;
  }
}

@media all and (max-width: 440px) {
  .balance-wr li b {
    display: flex;
    flex-flow: column;
  }

  .balance-wr li img{
    margin-right: 8px;
  }
}

.conversor-wr {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 24px auto;
  width: 100%;
}

@keyframes reveal {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.conversor {
  animation: reveal 1s ease-in-out forwards;
  background: #fff;
  border-radius: 16px;
  box-shadow: rgb(0 0 0 / 1%) 0px 0px 1px, rgb(0 0 0 / 4%) 0px 4px 8px,
    rgb(0 0 0 / 4%) 0px 16px 24px, rgb(0 0 0 / 1%) 0px 24px 32px;
  padding: 24px;
  opacity: 0;
  transform: translateY(24px);
  margin-bottom: 24px;
  max-width: 448px;
  width: calc(100% - 32px);
}

.note-instructions {
  animation: reveal 1s ease-in-out forwards;
  animation-delay: 0.25s;
  display: block;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  line-height: 1.5;
  max-width: 448px;
  opacity: 0;
  transform: translateY(16px);
  width: calc(100% - 32px);
}

.input-wr {
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  display: flex;
  flex-flow: column;
  padding: 12px;
  margin: 16px 0;
}

.input-wr-inner{
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.coin-name{
  align-items: center;
  display: flex;
}

.coin-name small{
  font-size: 14px;
  font-weight: 600;
  margin-left: 4px;
}

.input-wr img{
  width: 18px;
}

.input-wr label {
  font-size: 14px;
  padding-bottom: 4px;
}

.input-wr input {
  border: none;
  color: #000;
  font-family: "Manrope", sans-serif;
  font-size: 24px;
  font-weight: 600;
  width: 80%;
}

.input-wr input::-webkit-outer-spin-button,
.input-wr input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input-wr input[type=number] {
  -moz-appearance: textfield;
}

.input-wr input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.25);
}

.input-wr input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.25);
}

.input-wr input::placeholder {
  color: rgba(0, 0, 0, 0.25);
}

.input-wr input:focus {
  outline: none;
}

.conv-btn {
  background: #ea4c4c;
  border: none;
  border-radius: 12px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  padding: 16px 0;
  transition: 0.35s ease-in-out;
  width: 100%;
}

.conv-btn:hover {
  transform: scale(0.95);
}

.conv-btn:focus {
  outline: none;
}

.option-controller {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
  width: 100%;
}

.option-controller a {
  border: 1px solid #ea4c4c;
  color: #ea4c4c;
  font-weight: 500;
  font-size: 14px;
  padding: 8px 12px;
}

.option-controller a.active {
  background: #ea4c4c;
  color: #fff;
}

.option-controller a:first-of-type {
  border-radius: 24px 0 0 24px;
}

.option-controller a:last-of-type {
  border-radius: 0 24px 24px 0;
}
.wallet-status {
  align-items: center;
  display: flex;
  font-size: 13px;
  font-weight: 500;
  margin: 16px 0 0 0;
  justify-content: center;
}

.wallet-status a{
  color: #ea4c4c;
  margin-left: 8px;
  font-weight: 600;
  text-decoration: underline;
}

.status {
  background: #ec3f3f;
  border-radius: 50%;
  height: 10px;
  margin-right: 8px;
  position: relative;
  width: 10px;
}

@keyframes status {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.8;
  }

  100% {
    transform: translate(-50%, -50%) scale(2.5);
    opacity: 0;
  }
}

.status:after {
  animation: status 2s ease-in-out infinite forwards;
  background: #ec3f3f;
  border-radius: 50%;
  content: "";
  opacity: 0.8;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
}

@media all and (max-width: 500px){
  .conversor {
    max-width: 100%;
  }
}
footer {
  background-color: #fff;
  width: 100%;
}

footer .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px;
  padding-top: 16px;
}

.social-links {
  display: flex;
}

.social-links li {
  padding: 0 12px;
}

.social-links li:first-of-type {
  padding-left: 0;
}

.social-links li:last-of-type {
  padding-right: 0;
}

.social-links svg {
  width: 18px;
}

.ext {
  align-items: center;
  background: #ea4c4c;
  border-radius: 4px;
  color: #fff;
  display: flex;
  font-size: 14px;
  font-weight: 600;
  padding: 8px;
}

.ext svg {
  margin-left: 8px;
  width: 16px;
}

.ext svg path {
  fill: #fff;
}

